:root {
    --finnish-blue: #002f6c;
    --finnish-red: #C8102E;
    --finnish-yellow: #FFC72C;
    --border-rad: 10px;
}

body {
    background: #fafafa;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container{
    max-width: 1400px;
}

.row+.row {
    margin-top: 5px;
}

.control-group {
    display: inline-block;
    vertical-align: top;
}

.card-flex {
    /* We first create a flex layout context */
    display: flex;
    /* Then we define the flow direction 
     and if we allow the items to wrap 
   * Remember this is the same as:
   * flex-direction: row;
   * flex-wrap: wrap;
   */
    flex-flow: row wrap;
    /* Then we define how is distributed the remaining space */
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin: 10px;
    list-style: none;
    /* background:var(--finnish-blue); */
}

.l-stretch {
    flex: 2 2 200px;
    max-width: 600px;
    min-width: 600px;
}

.r-stretch {
    flex: 1 1 100px;
    max-width: 600px;
    min-width: 600px;
}

.word-card {
    /* border-radius:10px; */
    font-family: Comfortaa;
    margin-top: 10px;
    /* margin-bottom: 10px; */
    /* max-width: 600px; */
    min-width: 100px;
    word-break: break-all;
    white-space: normal;
}

.rcard {
    /* font-size: 1.5em; */
    /* min-width: 600px; */
    font-weight: 400;
    height: 100px;
    padding: 0;
    /* margin-left: 5px; */
    border-radius: 0 var(--border-rad) var(--border-rad) 0;
}

.no-l-padding{
    padding-left:0;
    padding-right:5px;
}

.no-r-padding{
    padding-left:5px;
    padding-right:0;
}

.lcard {
    font-size: 100px;
    font-weight: 700;
    height: 320px;
    /* margin-left: -5px; */
    /* margin-right: 5px; */
    border-radius: var(--border-rad) 0 0 var(--border-rad);
}

.card-text {
    text-align: center;
    overflow: hidden;
}

.rtext {
    line-height: 100px;
}

.ltext {
    line-height: 320px;
    color: var(--finnish-blue);
}

.word-card.blue {
    background: var(--finnish-blue);
    color: #fff;
}

.word-card.red {
    background: var(--finnish-red);
    color: var(--finnish-yellow);
}

.word-card.yellow {
    background: var(--finnish-yellow);
    color: var(--finnish-red);
}

.card.gray {
    background: #fff;
    width: 1100px;
    /* display: inline; */
    margin: 100px auto;
}

.no-margin {
    margin: 0;
    padding: 0;
}

.rcard-image {
    width: 100px;
    border-radius: 0;
    /*4px 0 0 4px;*/
}

.input-row{
    padding-right: 10px;
    padding-left: 10px;
}

